getCss.onclick = () => {
  const re = new XMLHttpRequest();
  re.open("GET", "/style.css");
  re.onload = () => {
    console.log("请求成功");
    console.log(re.response);
    const style = document.createElement("style");
    style.innerHTML = re.response;
    document.head.appendChild(style);
  };
  re.onerror = () => {
    console.log("请求失败");
  };
  re.send();
};
